<template>
  <div id="app">
    <div class="menu-header">
      제품 출고
    </div>
    <div class="center">
      <div class="box">
        <el-card>
          <el-row>
            <el-col :span="24">
            <div class="searchBody">
              <div class="searchBody_brand">
                Read 설정
              </div>
              <div>
                <template>
                  <el-radio v-model="readMode"  @change='setMode' label="1">BARCOODE</el-radio>
                  <el-radio v-model="readMode"  @change='setMode' label="2">RFID</el-radio>
                </template>
              </div>
            </div><!-- <div class="searchBody">-->
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="24">
            <div class="searchBody">

              <div class="searchBody_brand">
                BARCODE
              </div>
              <div>
                <el-input
                    id="epcInput"
                    size="small"
                    placeholder=""
                    v-model="barcode">
                </el-input>
              </div>

            </div><!-- <div class="searchBody">-->
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="24">
              <div class="searchBody">
              <el-button
                  :loading="loading"
                  type="primary"
                  @click="initBarcode"
              >초기화</el-button>
              </div>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="24">
              <div>
<!--                <RfidRead v-for="RfidRead in RfidReads"  :key="RfidRead.id"-->
<!--                          :RfidReads="RfidRead"/>-->

                <el-table
                    v-loading="loading"
                    ref="multipleTable"
                    :data="tableData"
                    style="width: 100%; height:372px"
                    border
                    @selection-change="handleSelectionChange"
                    :row-class-name="tableRowClassName"
                    size="small"
                    :row-style="{height:0+'px'}"
                    :cell-style="{padding:0+'px'}"
                    :header-cell-style="{background:'#f0f9eb',color:'#606266',  height:'10px', padding:'0px'}"
                >
                  <el-table-column
                      type="selection"
                      width="30"
                  >
                  </el-table-column>

                  <el-table-column
                      property="style"
                      label="style"
                      width="90">
                  </el-table-column>

                  <el-table-column
                      property="color"
                      label="color"
                      width="50">
                  </el-table-column>

                  <el-table-column
                      property="size"
                      label="size"
                      width="40">
                  </el-table-column>

                  <el-table-column
                      property="order_degree"
                      label="차수"
                      width="50">
                  </el-table-column>


                  <el-table-column
                      property="amount"
                      label="수량"
                      width="80">
                  </el-table-column>


                </el-table>

              </div>

            </el-col>
          </el-row>

          <el-row>
            <el-col :span="24">
              <div class="searchBody">

                <div>
                  <el-button
                      :loading="loading"
                      type="primary"
                      @click="remove"
                  >삭제</el-button>
                </div>

              <div>
              <el-button
                  :loading="loading"
                  type="primary"
                  @click="save"
              >작업저장</el-button>
              </div>

              <div>
                  <el-button
                      :loading="loading"
                      type="primary"
                      @click="appOutGroupList"
                  >작업목록</el-button>
              </div>

              <div>
              <el-button
                  :loading="loading"
                  type="primary"
                  @click="reading"
              >test Reading</el-button>
              </div>
              </div>
            </el-col>
          </el-row>

        </el-card>
      </div>
    </div>
  </div>


</template>

<script>
// import RfidRead from '@/views/components/RfidRead.vue';
import Axios from "axios";

export default {
  created() {
    window.getTag = this.addTodo
    window.reading = this.reading
    window.setBarcode = this.setBarcode;
    window.AlienMwApp.setMode('BARCODE');

    this.$store.commit("dayBefore",0);
    this.requestEndDateOption = this.$store.state.dateOption; // 조회조건에서 현재 날짜
    this.$store.commit("dayBefore",7);
    this.requestStartDateOption = this.$store.state.dateOption; // 조회조건이서 현재보다 -7 일

    if (this.$store.state.userId != 'admin')
      this.deptNameOption = this.$store.state.deptName;
    

  },

  components: {
    // RfidRead
  },
  data(){
    return {
      show: true,
      RfidReads :[
      ],
      tableData: [],
      totalData: [],
      epcData:[],
      saveData:[],
      multipleSelection: [],
      epc:1234567890,
      brandOption: '',
      styleOption :'',
      deptNameOption:'',
      publishAmmount:'',
      rfidTakeAmount:'',
      readMode:'1',
      barcode:'',
      seq:0,
    }
  },
  methods:{

    setBarcode(barCode){
      this.barcode =barCode;
      this.readMode = '2';
      window.AlienMwApp.setMode('RFID');
    },

    setMode(){
      if (this.readMode=='1')
      {
        console.log("barcode");
        window.AlienMwApp.setMode('BARCODE');
      }else {
        console.log("rfid");
        window.AlienMwApp.setMode('RFID');
      }
    },

    initBarcode(){
      this.barcode='';
    },//init
    appOutGroupList(){
      this.$router.push({ name: 'appOutGroupList' })
    },
    save(){

      if (this.multipleSelection.length <= 0)
      {
        this.$message.error("데이터를 선택하세요 ");
        return ;
      }

      if (this.barcode == '')
      {
        this.$message.error("BARCODE 값을 입력해주세요 ");
        return ;
      }

      for(var resData in this.epcData){
        this.epcData[resData]['reg_user_id'] = this.$store.state.userId
        this.epcData[resData]['upd_user_id'] = this.$store.state.userId
        this.epcData[resData]['dept_seq'] = this.$store.state.deptSeq
        this.epcData[resData]['w_in_box_barcode'] = this.barcode

        for(var tableData in this.multipleSelection) {
          if (this.multipleSelection[tableData]['seq'] == this.epcData[resData]['seq'] )
          {
            this.saveData.push( this.epcData[resData] );
          }
        }
      }

      this.loading = true;
      let url = this.$url + '/ProductionOutInfo/save';

      Axios.post(url, this.saveData , this.$store.state.config  ).then(res => {

        console.log(res );
        let saveCount = res.data;
        this.loading = false;
        this.$message.success(saveCount +'건 ' + this.$store.state.saveSuccess);
        this.remove();
        // this.barcode = '';
        this.saveData = [];

      }).catch(error => {
        console.log(error.response)
        this.$message.error(this.$store.state.serverErrorMessage);
        this.currentPage = 1;
        this.loading = false;
      }); // await Axios.post
    }, // save


    reading(){
      let style = 'style';
      let color = 'color'
      let size = 'size'
      // let order_degree ='01'
      let rfid_tag= 'rfid_tag';
      let flag = true;

      if (this.barcode == '')
      {
        this.$message.error("BARCODE 값을 입력해주세요 ");
        return ;
      }

      // 스타일 컬러 사이즈 확인 하는 로직

      for(var resData in this.tableData ){
        if (this.tableData[resData]['style'] == style)
        {
          this.tableData[resData]['amount'] =  this.tableData[resData]['amount'] + 1
          flag = false ;
        }
      }


      this.epc  = this.epc + 1

      if (flag == true){

        this.seq = this.seq + 1

        this.tableData.push({
          seq : this.seq
          , style: style
          , color: color
          , size: size
          , order_degree: this.seq
          , amount : 1
        });
      }

      this.epcData.push({
        dept_seq: this.$store.state.deptSeq
        ,seq : this.seq
        ,w_in_box_barcode : this.barcode
        ,epc : this.epc
        ,rfid_tag : rfid_tag
        ,style: style
        ,color: color
        ,size: size
        ,order_degree: this.seq
      });

      // window.AlienMwApp.playSound('success')

    }, //reading
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    tableRowClassName({row, rowIndex}) {
      if (rowIndex === 1) {
        //  return 'warning-row';
      } else if (rowIndex === 3) {
        //  return 'success-row';
      }
      console.log(row);
      return '';
    },
    handleSizeChange(val) {
      console.log(`${val} items per page`);
    },

    // pagging event
    handleCurrentChange(val) {
      this.tableData = this.totalData.slice( (this.pageSize * val - this.pageSize )   , this.pageSize * val);
    },

    remove(){
       if (this.multipleSelection.length <= 0)
       {
         this.$message.error("데이터를 선택하세요 ");
         return ;
       }

      for(var resData in this.multipleSelection) {

        for(var tableData in this.tableData) {
          if (this.multipleSelection[resData]['seq'] == this.tableData[tableData]['seq'] )
          {
            this.tableData.splice(tableData, 1);
          }
        }
      }

      resData = 0;
      tableData = 0;

      for( resData in this.multipleSelection) {
        for( tableData in this.epcData) {
          if (this.multipleSelection[resData]['seq'] == this.epcData[tableData]['seq'] )
          {
            this.epcData.splice(tableData, 1);
          }
        }
      }
    }, // remove
  }
}
</script>




<style scoped>
.box {
  flex: 1;
  display: flex;
  justify-content: center;
}

.searchBody_brand{
  width:60px !important;
  /* // border: 1px solid #f29659 !important; */
}

.searchBody_production{
  width:40px !important;
  /* // border: 1px solid #f29659 !important; */
  margin-left: 0px !important;
}

.el-row {
  margin-bottom: 2px;
}

</style>
<style lang="scss">

.box .el-card {
  width: 340px;
  height: 100%;
}

#storeInStartInputDate{
  width :100px  !important;
}
.searchBody_publish_amount{
  margin-left: 0px !important;
  margin-right: 10px !important;
}

#input_publish_amount{
  width:50px !important;
}

#input_rfid_take_amount{
  width:50px !important;
}

.searchBody_rfid_take_amount{
  margin-left: 10px !important;
  margin-right: 10px !important;
}

.menu-header {
  padding: 10px 20px;
  font-size: 15px !important;
  display: flex;
  flex-direction: column;
  align-items: center;
}

</style>
